import {
    Component,
    h,
    State,
    Element,
    EventEmitter,
    Event,
} from '@stencil/core';
import { fetchTranslations } from '../../../utils/translation';

/**
 * @category Data Tables
 * @description A menu component for exporting table data in CSV or PDF formats. Provides a user-friendly interface for exporting data from tables.
 */

@Component({
    tag: 'at-table-export-menu',
    shadow: false,
})
export class AtTableExportMenu {
    @Element() el: any;
    @State() translations: any;

    async componentWillLoad() {
        this.translations = await fetchTranslations(this.el);
    }

    /**
     * Emits id of the clicked menu item, either 'CSV' or 'PDF'.
     */
    @Event() atChange: EventEmitter<string>;

    render() {
        return (
            <at-menu
                width="fit-content"
                align="end"
            >
                <at-tooltip
                    slot="menu-trigger"
                    position="top"
                >
                    <at-button
                        slot="tooltip-trigger"
                        icon="save_alt"
                        type="secondaryText"
                    />
                    <span>{this.translations.ATUI.TABLE.EXPORT_TO_FILE}</span>
                </at-tooltip>
                <div>
                    <at-menu-item
                        label={this.translations.ATUI.TABLE.EXPORT_AS_CSV}
                        id="CSV"
                        onAtuiClick={() => this.atChange.emit('CSV')}
                    />
                    <at-menu-item
                        label={this.translations.ATUI.TABLE.EXPORT_AS_PDF}
                        id="PDF"
                        onAtuiClick={() => this.atChange.emit('PDF')}
                    />
                </div>
            </at-menu>
        );
    }
}
